.container {
  padding: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.container h1 {
  letter-spacing: 10px;
  color: #e0e0e0;
  font-size: 60px;
  text-transform: uppercase;
  text-shadow: -1px -1px 1px #b3b3b3, 2px 2px 1px #eff9ff;
}
