.container {
  padding: 1rem;
}
.submit {
  width: 100%;
  border: none;
  outline: none;
  height: 45px;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: box-shadow 0.2s linear;
  background: #858585;
  color: #fff;
}
.submit:hover {
  background: #e0e0e0;
  color: #000;
  box-shadow: 5px 5px 10px #bebebe, -5px -5px 10px #ffffff;
}
.submit:active {
  box-shadow: none;
}
