.container {
  width: 100%;
  min-height: 100vh;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e0e0e0;
}
.inner {
  width: 600px;
  border-radius: 1rem;
  background: #e0e0e0;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}

@media screen and (max-width: 768px) {
  .container {
    align-items: flex-start;
  }
  .inner {
    width: 100%;
  }
}
