.container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
.container label {
  cursor: pointer;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 700;
  width: max-content;
  color: #858585;
}
.container input {
  width: 100%;
  height: 45px;
  border: none;
  outline: none;
  font-size: 1rem;
  padding: 0rem 1rem;
  border-radius: 0.5rem;
  background: #e0e0e0;
  border: 1px solid rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.2s linear;
}
.container input::placeholder {
  color: #858585;
}
.container input:active,
.container input:focus {
  border: none;
  box-shadow: 5px 5px 10px #bebebe, -5px -5px 10px #ffffff;
}
